.main-nav {
  position: relative;
  display: flex;
  flex-wrap: wrap;
}

.main-nav__logo {
  display: block;
  min-width: 100%;
  height: 39px;
  text-align: center;
  padding: 22px 0 16px 0;
  background-color: $basic-white;
  mix-blend-mode: normal;
  box-shadow: 0 1px 0 $basic-grey-light, inset 0 1px 0 $basic-grey-light;

  &:hover {
    opacity: 0.6;
  }

  &:focus {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    min-width: 40%;
    text-align: left;
    height: 50px;
    flex-grow: 1;
    padding-left: 50px;
  }

  @media (min-width: $desktop-width) {
    min-width: auto;
    text-align: center;
    flex-grow: 1;
    padding: 9px 25px 0 0;
    height: 79px;
  }
}

.main-nav__logo img {
  width: 86px;
  height: 35px;

  @media (min-width: $tablet-width) {
    width: 112px;
    height: auto;
  }

  @media (min-width: $desktop-width) {
    width: 148px;
    height: auto;
  }
}

.main-nav__toggle {
  position: absolute;
  border: none;
  background: url("../img/icon-menu-close.svg") 0 0 no-repeat;
  width: 21px;
  height: 21px;
  top: 29px;
  right: 32px;

  &--open {
    background: url("../img/icon-menu-open.svg") 0 0 no-repeat;
    width: 20px;
    height: 14px;
    top: 32px;
    right: 31px;
  }


  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav__delivery {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    font-size: 15px;
    line-height: 24px;
    color: $basic-grey;
    order: 1;
    padding: 20px 50px 20px 0;
  }
}

.main-nav__list {
  &--close {
    display: none;
  }
}

.main-nav__user-block {
  &--close {
    display: none;
  }
}
